import React, { PropTypes } from 'react'
// css
import classes from '../../public/stylesheets/scss/detailInfo.scss'

class DetailInfo extends React.Component{
  constructor(props) {
    super(props)
  }
  render(){
    const items = {
      brand: '',
      subname: '',
      type: '',
      prefecture: '',
      brewery: '',
      url: 'URL',
      description: '',
      starterCulture: '',
      sakeYeast: '',
      sakeRiceExceptForKojiMaking: '',
      riceForMakingKoji: '',
      ricePolishingRate: '',
      alcoholContent: '',
      sakeMeterValue: '',
      acidity: '',
      aminoAcidContent: '',
    }
    let setAnchor = ( input ) => {
      if( /^http/.test( input ) ){
        return <a href={input} target="_blank">{input}</a>
      }
      return input
    }
    return(
    <table className={classes.table}>
          { Object.keys( items ).map( key => {
            return(
              <tr className={classes.tr}>
                <th className={classes.th} width="25%">{ items[key] }</th>
                <td className={classes.td} >
                    { setAnchor( this.props.sake[key] ) }
                </td>
              </tr>
            )
          } ) }
    </table>
    )
  }
}

DetailInfo.propTypes = {
  sake: PropTypes.object.isRequired,
}

export default DetailInfo



