import React, { PropTypes } from 'react'
// material-ui
import Chip from 'material-ui/Chip'
import { blue200, brown200, green200, orange200, purple200 } from 'material-ui/styles/colors'
// util
import concatObj from '../util/concatObj'

class TypeMark extends React.Component {
  judgeType( review ) {
    if( !(review.flavor && review.taste && review.maturation) ) {
      return ''
    }
    if( review.flavor <= 2 & review.taste <= 2 ){
      return <Chip backgroundColor={blue200}></Chip>
    }
    if( review.flavor <= 2 & 3 <= review.taste ){
      return <Chip backgroundColor={orange200}></Chip>
    }
    if( 2 <= review.flavor & review.taste <= 2 ){
      return <Chip backgroundColor={green200}></Chip>
    }
    if( 2 <= review.flavor & 3 <= review.taste &  3 <= review.maturation ){
      return <Chip backgroundColor={brown200}></Chip>
    }
    if( 2 <= review.flavor & 3 <= review.taste & review.maturation <= 2 ){
      return <Chip backgroundColor={purple200}></Chip>
    }
  }
  type() {
    return ''
  }

  render() {
    let styles = {
      typeMark: {
        margin: '1em 0 0 0',
      },
    }
    return (
      <div style={ concatObj( styles.typeMark, this.props.style ) }>{ this.judgeType( this.props.review ) }</div>
    )
  }
}

TypeMark.propTypes = {
  review: PropTypes.object.isRequired,
  style: PropTypes.object,
}

export default TypeMark



